<template>
<div style="height: 100%;">
  <div id="one">
    <!-- <div class="box">
      <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
      <a-button type="primary" icon="play-circle"   @click="attach">
        agent到进程上
      </a-button>
    </div> -->
    <div class="box">
      <a-input placeholder="请输入需要打印时间的类全路径（含包名）" v-model="className" />
      <a-input placeholder="请输入在该类中哪个方法上进行追加打印" v-model="methodName"/>
      <a-button type="primary" icon="play-circle"   @click="log">
        开始追加log
      </a-button>
      <div class="box"  >
        <a-button type="primary" icon="play-circle"   @click="clear">
          清空output-console
        </a-button>
      </div>
    </div>

  </div>
  <div id="two">
    <a-table :columns="columns" :data-source="data" :pagination="false">
      <span slot="name" slot-scope="text" style="white-space: pre-line; color: #165be0" >{{ text }}</span>
      <span slot="customTitle"> output-console</span>
    </a-table>
  </div>
</div>
</template>

<script>

const columns = [
  {
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  }
];
const data = [

];
export default {
  data() {
    return {
      data,
      columns,
      className:'',
      methodName: '',
      webSock: null,
      javaId:'',
    };
  },
  created() {
    var pid = sessionStorage.getItem("pid")  
    if(pid === null){
         this.$router.push("/javaId");
    }
    let ip = sessionStorage.getItem("ip")
    const wsuri = "ws://"+ip+":9675/ws/ws";
    this.webSock = new WebSocket(wsuri);
    this.webSock.onmessage = this.webSocketOnMessage;
    this.log()

  },
  destroyed: function () {
    this.webSock.close();
  },
  methods:{
    log(){
      this.webSock.send(`aop ${this.className} ${this.methodName}`);
    },
    clear(){
      this.data = [];
    },
    attach(){
        var pid = sessionStorage.getItem("pid")  
      this.webSock.send("pid="+pid);
    },
    sizeCheck(msg){
      if(this.data.length > 10){
        this.data.shift();
        this.data.push(msg);
      }else{
        this.data.push(msg);
      }
    },
    jps() {
      let msg = {"name":"获取Java进程Pid"}
      this.sizeCheck(msg)
      this.webSock.send("jps");
    },
    webSocketOnMessage(e){
      if(!String(e.data).startsWith('ds')){
        let msg = {"name":e.data}
        this.sizeCheck(msg)
      }
    },
  },

  name: "BuildLog"
}
</script>

