<template>
  <div class="box">
    <a-input placeholder="输入IP地址,本地就localhost,否则就是外网ip" v-model="ip" />
    <div class="box">
      <a-button type="primary" icon="play-circle"   @click="setIp">
        配置ip
      </a-button>
    </div>

  </div>
</template>

<script>
export default {
name: "IPset",
  destroyed: function () {
    this.webSock.close();
  },
  created() {
    let ip = sessionStorage.getItem("ip")
    const wsuri = "ws://"+ip+":9675/ws/ws";
    this.webSock = new WebSocket(wsuri);
    this.webSock.onmessage = this.webSocketOnMessage;
  },
  data() {
    return {
      ip: '',
      webSock:null,
    };
  },
  methods:{
    webSocketOnMessage(e){
       this.$message.info(e.data);
    },
    findIp(){
      this.webSock.send("ip")
    },
    setIp(){
      if(this.ip !== ''){
        sessionStorage.setItem("ip",this.ip)
      }
      this.$message.info("配置成功！")

    }
  },
}
</script>

<style scoped>
.box{
  vertical-align:middle;
  display:inline-block;
  width: 500px;

}
</style>