<template>
  <div>
   <!-- <div v-if="this.obj == null">
      <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
      <a-button type="primary" icon="play-circle"  style="margin-bottom: 10px" @click="attach">
        agent到进程上
      </a-button>
    </div> -->
    <div v-if="this.obj != null">
      <a-list :grid="{ gutter: 16, column: 4 }" :data-source="data">
        <a-list-item slot="renderItem" slot-scope="item">
          <a-card :title="item.title">
            {{item.value}}
          </a-card>
        </a-list-item>
      </a-list>
    </div>
  </div>

</template>

<script>
const data = [
];
export default {
  name: "Monitor",
  data() {
    return {
      data,
      webSock: null,
      obj: null,
      javaId:''
    };
  },
  destroyed: function () {
    this.webSock.close();
    clearInterval(this.inv)
  },
  created() {
      var pid = sessionStorage.getItem("pid")
      if(pid === null){
           this.$router.push("/javaId");
      }
    this.conn();
  },
  methods:{
    conn(){
      let ip = sessionStorage.getItem("ip")
      const wsuri = "ws://"+ip+":9675/ws/ws";
      this.webSock = new WebSocket(wsuri);
      this.webSock.onmessage = this.webSocketOnMessage;
    },
    webSocketOnMessage(e){
      if("连接成功，等待发送指令" === e.data){
        this.$message.info("连接成功")
        this.send();
      }else if("ETrace已经准备就绪" === e.data){
        this.$message.info("agent准备就绪")
        this.send();
      }else if("仅支持linux" === e.data){
        this.$message.error('仅支持linux')
      }else{
        this.data = []
         console.log(JSON.parse(e.data)['java.ext.dirs'])
        let obj = JSON.parse(e.data)
        console.log(obj)
        this.obj = obj
        this.data.push(
            {"title":'时区',"value":obj['user.timezone']},
            {"title":'工作目录',"value":obj['user.dir']},
            {"title":'jar',"value": obj['java.class.path']},
            {"title":'版本',"value":   obj['java.version']},
            {"title":'javaHome',"value":    obj['java.home']},
            {"title":'JVM',"value":   obj['java.vm.name']},
            {"title":'虚拟机参数',"value":   obj['flag']},
        )
      }
    },
    send(){
      if(WebSocket.OPEN === this.webSock.readyState){
        var pid = sessionStorage.getItem("pid")
        this.webSock.send("javainfo " +pid)
      }else{
        this.conn()
      }

    },
    attach(){
          var pid = sessionStorage.getItem("pid")  
      this.webSock.send("pid="+pid);
    },
  }
}
</script>

