<template>
  <div>
      <!-- <div v-if="this.obj == null">
         <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
         <a-button type="primary" icon="play-circle"  style="margin-bottom: 10px" @click="attach">
           agent到进程上
         </a-button>
       </div> -->
        <template>
          <a-spin v-if="this.loddingShow === true && this.obj===null" />
            <a-table :columns="columns"
                     :data-source="data"
                     :pagination="false"
                     :customRow="click"
                     v-if="this.obj != null">
              <a slot="name" slot-scope="text">{{ text }}</a>
            </a-table>
          <a-modal
              title="堆栈信息"
              :visible="visible"
              :width="800"
              @ok="handleOk"
              @cancel="handleCancel"
          >
           <span style="white-space: pre-line;">{{ ModalText }}</span>
          </a-modal>
        </template>

  </div>
</template>
<script>
const columns = [
  {
    title: '线程ID',
    dataIndex: 'threadId',
    key: 'threadId',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: '名称',
    dataIndex: 'threadName',
    key: 'threadName',
  },
  {
    title: '分组',
    dataIndex: 'groupName',
    key: 'groupName',
  },
  {
    title: '优先级',
    dataIndex: 'priority',
    key: 'priority',
  },
  {
    title: 'CPU时间占比',
    dataIndex: 'cpu',
    key: 'cpu',
  },
  {
    title: '是否是守护进程',
    dataIndex: 'daemon',
    key: 'daemon',
  },
  {
    title: '线程状态',
    dataIndex: 'threadState',
    key: 'threadState',
  },
];

const data = [

];
export default {
name: "Monitor",
  data() {
    return {
      data,
      columns,
      webSock: null,
      obj: null,
      javaId:'',
      show: true,
      ModalText: 'empty info !',
      visible: false,
      confirmLoading: false,
      inv: null,
      loddingShow:false
    };
  },
  destroyed: function () {
    this.webSock.close();
    clearInterval(this.inv)
  },
  created() {
       this.loddingShow = true;
      var pid = sessionStorage.getItem("pid")
      if(pid === null){
           this.$router.push("/javaId");
      }
    this.conn();
  },
  methods:{
    conn(){
      let ip = sessionStorage.getItem("ip")
      const wsuri = "ws://"+ip+":9675/ws/ws";
      this.webSock = new WebSocket(wsuri);
      this.webSock.onmessage = this.webSocketOnMessage;
    },
    click(record){
      return {
        on: {
          click: () => {
            this.webSock.send("threadstack "+record.threadId)
            this.visible = true;
          }
        }
      }
    },
    handleOk() {
      this.visible = false;
    },
    handleCancel() {
      this.visible = false;
    },
    webSocketOnMessage(e){
      if("连接成功，等待发送指令" === e.data){
        this.go();
      }else if("agent已经准备就绪" === e.data){
        this.$message.info("agent准备就绪")
        this.go();
      }else if("agent已经存在,接收到重连信息，重连成功.." === e.data){
        this.$message.info("agent重连成功")
      }else{
        if(String(e.data).substr(0,2) === 'ds'){
          if (String(e.data).substr(0,8) === 'dsThread'){
            this.obj =  JSON.parse(String(e.data).substr(8));
            this.data = this.obj
          }
        }else{
          this.ModalText = e.data.replace(/\\n/g, '\n')
          if("未Attach或目标进程已关闭" === e.data){
              this.$message.error(e.data)
          }
        }
      }
    },
    go() {
      this.show = false;
      this.send()
      if(this.inv === null){
        this.inv = setInterval( this.send, 2000);
      }
    },
    send(){
      if(WebSocket.OPEN === this.webSock.readyState){
        this.webSock.send("threads")
      }
    },
    attach(){
      this.loddingShow = true;
      this.webSock.send("pid="+this.javaId);
    },
  }
}
</script>

<style scoped>
.spin-content {
  border: 1px solid #91d5ff;
  background-color: #e6f7ff;
  padding: 30px;
}
</style>