<template>
  <div style="height: 100%;">
    <div id="one">
   <!-- <div class="box">
      <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
      <a-button type="primary" icon="play-circle"   @click="attach">
        agent到进程上
      </a-button>
    </div> -->
    <div class="box">
      <a-button type="primary" icon="play-circle"   @click="cpuTop">
         打印线程Id和占用cpu百分比
      </a-button>
    </div>
      <div class="box">
        <a-input placeholder="请输入线程id，例如:73322" v-model="threadId" />
        <a-button type="primary" icon="play-circle"   @click="stack">
          打印线程id{{threadId}}的堆栈信息
        </a-button>
      </div >
      <div class="box"  >
        <a-button type="primary" icon="play-circle"   @click="clear">
          清空output-console
        </a-button>
      </div>
    </div>
    <div id="two">
      <a-table :columns="columns" :data-source="data" :pagination="false">
        <span slot="name" slot-scope="text" style="white-space: pre-line; color: #165be0" >{{ text }}</span>
        <span slot="customTitle"> output-console</span>
      </a-table>
    </div>
  </div>

</template>

<script>
const columns = [
  {
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  }
];
const data = [
];
export default {
  data() {
    return {
      data,
      columns,
      className:'',
      methodName: '',
      webSock: null,
      javaId:'',
      threadId:'',
    };
  },
  destroyed: function () {
    this.webSock.close();
  },
  created() {
      var pid = sessionStorage.getItem("pid")
      if(pid === null){
           this.$router.push("/javaId");
      }
    let ip = sessionStorage.getItem("ip")
    const wsuri = "ws://"+ip+":9675/ws/ws";
    this.webSock = new WebSocket(wsuri);
    this.webSock.onmessage = this.webSocketOnMessage;
  },
  methods:{
    cpuTop(){
         var pid = sessionStorage.getItem("pid")
        this.webSock.send(`cpuTop ` + pid);
    },
    clear(){
      this.data = [];
    },
    sizeCheck(msg){
      if(this.data.length > 10){
        this.data.shift();
        this.data.push(msg);
      }else{
        this.data.push(msg);
      }
    },
    stack(){
        this.webSock.send(`stack ${this.threadId}`);

    },
    time(){
      this.webSock.send(`time ${this.className} ${this.methodName}`);
    },
    jps() {
      this.webSock.send("jps");
    },
    attach(){
      this.webSock.send("pid="+this.javaId);
    },
    webSocketOnMessage(e){
      if(!String(e.data).startsWith('ds')){
        let msg = {"name":e.data}
        this.sizeCheck(msg)
      }
    },
  },

name: "Stack"
}
</script>

