<template>
  <div style="height: 100%;">
    <div id="one">
      <div class="box">
        <a-button type="primary" icon="play-circle"  :loading="iconLoading" @click="helpMe">
          Help?
        </a-button>
      </div>
<!--      <div class="box">-->
<!--        <a-button type="primary" icon="play-circle"  :loading="iconLoading" @click="conn">-->
<!--          开始连接到agent-->
<!--        </a-button>-->
<!--      </div>-->
 <!--     <div class="box">
        <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
        <a-button type="primary" icon="play-circle"  :loading="iconLoading" @click="attach">
          agent到进程上
        </a-button>
      </div> -->
      <div class="box">
        <a-input placeholder="请输入需要反编译的文件名称，例如:com.User" v-model="fbyJavaFileName" />
        <a-input placeholder="请输入需要反编译到哪个目录，例如:/home/ ,最后有要有/" v-model="fbyJavaFileDir" />
        <a-button type="primary" icon="undo"  :loading="iconLoading" @click="exportClazz" style="margin-top: 5px">
          反编译{{fbyJavaFileName}}到目录{{ fbyJavaFileDir }}
        </a-button>
      </div>
      <div class="box">
        <a-input placeholder="请输入需要编译的class文件名称，例如:/home/User.java" v-model="byClassFileName"  />
        <a-input placeholder="请输入需要编译到哪个目录，例如:/home/ ,最后有要有/" v-model="byClassFileDir" />
        <a-button type="primary"  icon="redo" :loading="iconLoading" @click="showConfirm" style="margin-top: 5px">
          编译{{byClassFileName}}到目录{{byClassFileDir}}
        </a-button>
      </div>
      <div class="box">
        <a-input placeholder="请输入编译好的class文件地址，例如:/home/User.class" v-model="redefineDir" />
        <a-button type="primary" icon="play-circle"  :loading="iconLoading" @click="redefine">
          redefine
        </a-button>
      </div>
      <div class="box"  >
        <a-button type="primary" icon="play-circle"   @click="clear">
          清空output-console
        </a-button>
      </div>
    </div>

    <div id="two">
      <a-table :columns="columns" :data-source="data" :pagination="false">
        <span slot="name" slot-scope="text" style="white-space: pre-line; color: #165be0" >{{ text }}</span>
        <span slot="customTitle"> output-console</span>
      </a-table>
    </div>

  </div>
</template>
<script>

const columns = [
  {
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  }
];

const data = [

];
export default {
  data() {
    return {
      webSock: null,
      data,
      javaId:'',
      columns,
      fbyJavaFileName: '',
      fbyJavaFileDir: '',
      fbyClassFileDir:'',
      byClassFileName:'',
      byClassFileDir:'',
      redefineDir:'',
      loading: false,
      iconLoading: false,
    };
  },
  created() {
      var pid = sessionStorage.getItem("pid")
      if(pid === null){
           this.$router.push("/javaId");
      }
    this.initWebSocket();
    this.helpMe();
  },
  destroyed: function () {
    this.webSock.close();
  },
  methods: {
    initWebSocket(){
      let ip = sessionStorage.getItem("ip")
      const wsuri = "ws://"+ip+":9675/ws/ws";
      this.webSock = new WebSocket(wsuri);
      this.webSock.onmessage = this.webSocketOnMessage;
      this.webSock.onopen = this.webSocketOnOpen;
      this.webSock.onerror = this.webSocketOnError;
      this.webSock.onclose = this.webSocketClose;
    },
    webSocketOnOpen(){
      console.log("open")
    },
    webSocketOnError(){
      this.initWebSocket();
    },
    webSocketOnMessage(e){
      if(!String(e.data).startsWith('ds')){
        let msg = {"name":e.data}
        this.sizeCheck(msg)
      }
    },
    sizeCheck(msg){
      if(this.data.length > 10){
        this.data.shift();
        this.data.push(msg);
      }else{
        this.data.push(msg);
      }
    },
    clear(){
      this.data = [];
    },
    webSocketSend(Data){
      this.webSock.send(Data);
    },
    webSocketClose(e){  //关闭
      console.log(e)
      let msg = {"name":"连接已经断开"}
      this.sizeCheck(msg)
    },
    enterLoading() {
      this.loading = true;
    },
    jps() {
      let msg = {"name":"获取Java进程Pid"}
      this.sizeCheck(msg)
      this.webSock.send("jps");
    },
    attach(){
      let msg = {"name":"开始Agent到Java进程"+this.javaId+""}
      this.sizeCheck(msg)
      this.webSock.send("pid="+this.javaId);
    },
    clazz(){
      let msg = {"name":"获取已加载类"}
      this.sizeCheck(msg)
      this.webSock.send("class");
    },
    exportClazz(){
      let fileName = this.fbyJavaFileName;
      let fileDir = this.fbyJavaFileDir;
      let msg = {"name":"将类"+fileName+"反编译到"+fileDir}
      this.sizeCheck(msg)
      this.webSock.send("export "+fileName+" "+fileDir+"");
    },
    redefine(){
      let fileDir = this.redefineDir;
      let msg = {"name":"将"+fileDir+"重新reload"}
      this.sizeCheck(msg)
      this.webSock.send("redefine "+fileDir);
    },
    change(){
      this.$message.info(this.fbyJavaFileName);
      this.$message.info(this.fbyJavaFileDir);
    },
    change1(){
      this.$message.info(this.byClassFileName);
      this.$message.info(this.byClassFileDir);
    },
    showConfirm() {
      let dir = this.byClassFileDir;
      let java = this.byClassFileName;
      let msg = this.$message;
      let sock = this.webSock;
      let check = this.sizeCheck;
      this.$confirm({
        title: '确认要开始编译?',
        cancelText: '取消',
        okText: '编译',
        content: `编译前请确认${dir===''?'':'在'+dir+'目录中的'}Java文件${java===''?'':java}是否已经修改过？`,
        onOk() {
          msg.info("开始编译操作");
          let datas = {"name":"将"+java+"编译到"+dir+"目录"}
          check(datas)
          sock.send("compile "+dir+" "+java);
        },
        onCancel() {
          msg.info("编译取消");
        },
      });
    },
    helpMe() {
      this.$notification.open({
        message: '操作指南',
        duration: 10,
        description:
            '请先点击连接到agent,若您知道修复的类全路径（含包名）,可跳过显示已加载类，点击显示Java进程，找到目标进程的Pid后,点击agent到进程，然后找到对应的类反编译到目录，然后手动修复bug，然后编译到目录，最后redefine即可修复成功',
        onClick: () => {
          console.log('Notification Clicked!');
        },
      });
      },
    enterIconLoading() {
      this.iconLoading = { delay: 0 };
    },
  },
};
</script>
