
import Vue from "vue";
import VueRouter from "vue-router";
//组件模块
import Login from './components/Login'
import Home from './components/Home'
import ReloadClass from './components/ReloadClass'
import BuildTime from './components/BuildTime'
import Stack from './components/Stack'
import IPS from './components/IPset'
import Log from './components/BuildAop'
import Monitor from './components/Monitor'
import JavaId from './components/JavaId'
import Threads from './components/Threads'
import Javainfo from './components/JavaInfo'


Vue.use(VueRouter);
export default new  VueRouter(
    {
            routes: [
                {
                    path: '/',
                    name: 'login',
                    component: Login
                },
                {
                    path: '/home',
                    name: 'home',
                    component: Home,
                    children:[
                        {
                            path: '/hotReload',
                            name: 'reloadClass',
                            component: ReloadClass
                        },
                        {
                            path: '/buildTime',
                            name: 'buildTime',
                            component: BuildTime
                        },
                        {
                            path: '/stack',
                            name: 'stack',
                            component: Stack
                        },
                        {
                            path: '/ip',
                            name: 'IPset',
                            component: IPS
                        },
                        {
                            path: '/log',
                            name: 'Log',
                            component: Log
                        },
                        {
                            path: '/monitor',
                            name: 'Monitor',
                            component: Monitor
                        },
                        {
                            path: '/javaId',
                            name: 'JavaId',
                            component: JavaId
                        },
                        {
                            path: '/threads',
                            name: 'Threads',
                            component: Threads
                        },
                        {
                            path: '/javainfo',
                            name: 'Javainfo',
                            component: Javainfo
                        },
                    ],
                }]
   }
)
