<template>
  <div>
      <!-- <div v-if="this.obj == null" class="box">
         <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
         <a-button type="primary" icon="play-circle"  style="margin-bottom: 10px" @click="attach">
           agent到进程上
         </a-button>
       </div> -->
       <a-spin v-if="this.loddingShow === true && this.obj===null" />
        <template>
          <a-descriptions title="数据监控" layout="vertical" bordered v-if="this.obj != null">
            <a-descriptions-item label="最大堆内存" >
              {{ this.obj.memoryInfo.heapMax }}
            </a-descriptions-item>
            <a-descriptions-item label="已使用堆内存">
              {{ this.obj.memoryInfo.heapUsed }}
            </a-descriptions-item>
            <a-descriptions-item label="提交的堆内存" :span="3">
              {{ this.obj.memoryInfo.heapCommitted }}
            </a-descriptions-item>
            <a-descriptions-item label="最大非堆内存"  >
              {{ this.obj.memoryInfo.noHeapMax }}
            </a-descriptions-item>
            <a-descriptions-item label="已使用非堆内存">
              {{ this.obj.memoryInfo.noHeapUsed }}
            </a-descriptions-item>
            <a-descriptions-item label="提交的非堆内存" :span="3">
              {{ this.obj.memoryInfo.noHeapCommitted }}
            </a-descriptions-item>

            <a-descriptions-item label="程序启动时间">
              {{ this.obj.systemInfo.startDate }}
            </a-descriptions-item>
            <a-descriptions-item label="程序已运行时间" :span="2">
              <div v-if="this.obj.systemInfo.upTime/1000 <= 60">
                {{ parseFloat(this.obj.systemInfo.upTime/1000).toFixed(2) }}秒
              </div>
              <div v-if="this.obj.systemInfo.upTime/1000 > 60 && this.obj.systemInfo.upTime/1000 < 3600">
                {{ parseFloat(this.obj.systemInfo.upTime/1000/60).toFixed(2) }}分钟
              </div>
              <div v-if="this.obj.systemInfo.upTime/1000 > 3600">
                {{ parseFloat(this.obj.systemInfo.upTime/1000/60/60).toFixed(2) }}小时
              </div>

            </a-descriptions-item>
<!--            <a-descriptions-item label="Status" :span="3">-->
<!--              <a-badge status="processing" text="Running" />-->
<!--            </a-descriptions-item>-->
            <a-descriptions-item label="线程数">
              {{ this.obj.threadInfoList.threadCount }}
            </a-descriptions-item>
            <a-descriptions-item label="线程数峰值">
              {{ this.obj.threadInfoList.peekCount }}
            </a-descriptions-item>
            <a-descriptions-item label="守护线程数">
              {{ this.obj.threadInfoList.threadCount }}
            </a-descriptions-item>
            <a-descriptions-item label="系统参数">
              <a-tag color="purple">
                系统名称:
              </a-tag>
               {{ this.obj.systemInfo.osName }}
              <br />
              <a-tag color="cyan">
                Java版本:
              </a-tag>
              {{ this.obj.systemInfo.javaVersion }}
              <br />
              <a-tag color="orange">
                JavaHome:
              </a-tag>
               {{ this.obj.systemInfo.javaHome }}
              <br />
              <a-tag color="red">
                处理器核心数:
              </a-tag>
               {{ this.obj.systemInfo.coreCount }}
              <br />
            </a-descriptions-item>
            <a-descriptions-item label="GC信息" :span="2">
              <div v-for="(gcItem,index) in obj.gcInfo" v-bind:key="index">
                <div style="text-align:left; ">
                  {{gcItem.gcName}}
                  <a-tag color="green">
                    回收次数:
                  </a-tag>
                  {{gcItem.gcCount}}
                  <a-tag color="blue">
                    回收时间:
                  </a-tag>
                  {{gcItem.gcTime}}ms
                  <br />
                </div>
              </div>
            </a-descriptions-item>
          </a-descriptions>
          <a-table :columns="gcHeapColumns" :data-source="gcHeapData" :pagination="false" v-if="this.obj != null">
            <a slot="name" slot-scope="text">{{ text }}</a>
          </a-table>
          <a-table :columns="gcNoHeapColumns" :data-source="gcNoHeapData" :pagination="false" v-if="this.obj != null">
            <a slot="name" slot-scope="text">{{ text }}</a>
          </a-table>
          <a-table :columns="getInfosColumns" :data-source="gcInfoData" :pagination="false" v-if="this.obj != null">
            <a slot="name" slot-scope="text">{{ text }}</a>
          </a-table>
            <a-table :columns="columns" :data-source="data" :pagination="false" v-if="this.obj != null">
              <a slot="name" slot-scope="text">{{ text }}</a>
            </a-table>
        </template>

  </div>
</template>
<script>
const getInfosColumns = [
  {
    title: '启动参数',
    width: 200,
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  }
]
const gcInfoData = []
const gcHeapColumns = [
  {
    title: '堆信息',
    width: 200,
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: '最大可用',
    width: 200,
    dataIndex: 'max',
    key: 'max',
  },
  {
    title: '已用',
    width: 200,
    dataIndex: 'used',
    key: 'used',
  },
  {
    title: '已提交',
    width: 200,
    dataIndex: 'committed',
    key: 'committed',
  },
]
const gcHeapData = []
const gcNoHeapColumns = [
  {
    title: '非堆信息',
    width: 200,
    dataIndex: 'name',
    key: 'name',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: '最大可用',
    width: 200,
    dataIndex: 'max',
    key: 'max',
  },
  {
    title: '已用',
    width: 200,
    dataIndex: 'used',
    key: 'used',
  },
  {
    title: '已提交',
    width: 200,
    dataIndex: 'committed',
    key: 'committed',
  },
]
const gcNoHeapData = []
const columns = [
  {
    title: '线程TOP10',
    dataIndex: 'threadId',
    key: 'threadId',
    slots: { title: 'customTitle' },
    scopedSlots: { customRender: 'name' },
  },
  {
    title: '名称',
    dataIndex: 'threadName',
    key: 'threadName',
  },
  {
    title: '分组',
    dataIndex: 'groupName',
    key: 'groupName',
  },
  {
    title: '优先级',
    dataIndex: 'priority',
    key: 'priority',
  },
  {
    title: '线程CPU',
    dataIndex: 'cpu',
    key: 'cpu',
  },
  {
    title: '是否是守护进程',
    dataIndex: 'daemon',
    key: 'daemon',
  },
  {
    title: '线程状态',
    dataIndex: 'threadState',
    key: 'threadState',
  },
];

const data = [

];
export default {
name: "Monitor",
  data() {
    return {
      gcHeapData,
      gcNoHeapData,
      gcHeapColumns,
      gcNoHeapColumns,
      gcInfoData,
      getInfosColumns,
      data,
      columns,
      webSock: null,
      obj: null,
      javaId:'',
      show: true,
      inv: null,
      loddingShow:false
    };
  },
  destroyed: function () {
    this.webSock.close();
    clearInterval(this.inv);
  },
  created() {
       this.loddingShow = true;
      var pid = sessionStorage.getItem("pid")
      if(pid === null){
           this.$router.push("/javaId");
      }
   this.conn();
   this.go();
  },
  methods:{
    conn(){
      let ip = sessionStorage.getItem("ip")
      const wsuri = "ws://"+ip+":9675/ws/ws";
      this.webSock = new WebSocket(wsuri);
      this.webSock.onmessage = this.webSocketOnMessage;
    },
    webSocketOnMessage(e){
      if("连接成功，等待发送指令" === e.data){
        //pass
      }else if("agent已经准备就绪" === e.data){
        this.$message.info("ETrace准备就绪")
        
        this.go();
      }else if("agent已经存在,接收到重连信息，重连成功.." === e.data){
        this.$message.info("ETrace重连成功")
      }else{
        this.gcInfoData = []
        if(String(e.data).substr(0,2) === 'ds'){
          if (String(e.data).substr(0,9) === 'dsMonitor'){
            this.obj =  JSON.parse(String(e.data).substr(9));
            this.data = this.obj.threadInfoList.threads
            this.gcHeapData = this.obj.memoryInfo.heapPoolInfos
            this.gcNoHeapData = this.obj.memoryInfo.noHeapPoolInfos
            for (var i in this.obj.infos){
              let msg = {"name":this.obj.infos[i]}
              this.gcInfoData.push(msg)
            }
            // console.log(this.obj.infos)
            // let msg = {"name":this.obj.infos}
            // this.gcInfoData = msg;
          }
        }else{
             this.$message.error(e.data)
        }
      }
    },
    go() {
      this.show = false;
      this.send()
      if(this.inv === null){
        this.inv = setInterval( this.send, 2000);
      }

    },
    send(){
      if(WebSocket.OPEN === this.webSock.readyState){
        this.webSock.send("monitor")
      }else{
        this.conn()
      }

    },
    attach(){
      this.loddingShow = true;
      this.webSock.send("pid="+this.javaId);
    },
  }
}
</script>

